<template>
  <div class="editor">
    <div class="main">
      <div class="content">
        <el-input placeholder="你见过哪篇文章没有标题的" v-model.trim="article_info.article_title"></el-input>
        <div class="d-twoBlock"></div>
        <div class="wangeditor"></div>
        <div class="d-fourBlock"></div>
        <el-button type="primary" size="small" @click="dialogVisible = true">发布</el-button>


        <el-dialog title="别发布来路不明的文章" :visible.sync="dialogVisible" width="500px" :before-close="handleCloseDialog">
          <!-- 添加标签 -->
          <el-tag :key="tag" v-for="tag in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)">
            {{tag}}
          </el-tag>
          <el-input class="input-new-tag" v-if="inputVisible" v-model.trim="inputValue" ref="saveTagInput" size="small"
            @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm">
          </el-input>
          <el-button v-else class="button-new-tag" size="small" @click="showInput">文章标签</el-button>

          <div class="d-twoBlock"></div>

          <!-- 文章类型 -->
          <el-select v-model="article_info.article_type" placeholder="文章类型">
            <el-option v-for="item in article_type" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>

          <!-- 文章分类 -->
          <el-select v-model="article_info.article_categories" placeholder="文章分类">
            <el-option v-for="item in article_classify" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>


          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="publishArticle">发 布</el-button>
          </span>
        </el-dialog>


      </div>
    </div>
  </div>
</template>

<script>
  import E from 'wangeditor'
  export default {
    name: 'Editor',
    data() {
      return {
        url: '/article',
        editor: null,
        editor_value: '',
        article_title: '',
        dialogVisible: false,
        dynamicTags: [],
        inputVisible: false,
        inputValue: '',
        article_type: [{
          value: '原创',
          label: '原创'
        }, {
          value: '转载',
          label: '转载'
        }],
        type_value: '',
        article_classify: [{
            value: '技术',
            label: '技术'
          },
          {
            value: '搞笑',
            label: '搞笑'
          },
          {
            value: '程序猿',
            label: '程序猿'
          },
          {
            value: '生活',
            label: '生活'
          },
          {
            value: '体育',
            label: '体育'
          },
          {
            value: '情感',
            label: '情感'
          },
          {
            value: '其他',
            label: '其他'
          }
        ],
        classify_value: '',
        article_info: {
          article_title: '',
          author: '',
          original_author: '',
          article_title: '',
          article_content: '',
          article_tags: '',
          article_type: '',
          article_categories: '',
          publish_date: '',
          update_date: '',
          article_url: '',
          article_tabloid: '',
          likes: '',
          last_articleId: '',
          next_articleId: ''
        }
      }
    },
    mounted() {
      this.createEditor();
    },
    methods: {
      createEditor: function() {
        this.editor = new E(".wangeditor");
        // 开始配置富文本编辑器
        this.editor.config.placeholder = '哇！这里可以写内容！图片暂时仅支持网络图片';
        this.editor.config.zIndex = 1;
        this.editor.config.menus = [
          'head',
          'bold',
          'fontSize',
          'fontName',
          'italic',
          'underline',
          'strikeThrough',
          'indent',
          'lineHeight',
          'foreColor',
          'backColor',
          'link',
          'list',
          'justify',
          'quote',
          'code',
          'splitLine',
          'undo',
          'redo',
          'image'
        ];
        this.editor.create()
      },
      // 发布文章
      publishArticle: function() {
        this.article_info.article_content = this.editor.txt.html(); // 获取正文内容
        this.article_info.article_tabloid = this.editor.txt.text().slice(0, 150) + '...'; // 截取正文内容150个字符 + ...
        this.article_info.article_tags = this.dynamicTags.join(","); // 文章标签
        this.article_info.author = this.$store.state.login_name;
        this.article_info.original_author = this.article_info.author;
        if (this.article_info.article_title != "" && this.article_info.article_content != "" && this.dynamicTags.length <=
          5 && this.article_info.article_type != "" && this.article_info.article_categories != "" && this.dynamicTags
          .length >= 1) {
          this.$axios.post(this.url + '/publish', this.article_info).then(res => {
            if (res.data.code == 200) {
              // 清空编辑器
              this.article_info = {
                article_title: '',
                author: '',
                original_author: '',
                article_title: '',
                article_content: '',
                article_tags: '',
                article_type: '',
                article_categories: '',
                publish_date: '',
                update_date: '',
                article_url: '',
                article_tabloid: '',
                likes: '',
                last_articleId: '',
                next_articleId: ''
              };
              this.editor.txt.html('');

              // 弹出框消失
              this.dialogVisible = false;

              // 提示信息
              this.$message({
                message: res.data.message,
                type: 'success'
              });
            } else {
              this.$message({
                message: res.data.message,
                type: 'warning'
              });
            }
          })
        } else {
          this.$message({
            message: '内容不要忘！标签数量不能超过5个',
            type: 'warning'
          });
        }
      },
      // 弹出框
      handleCloseDialog(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      // 添加标签
      handleClose(tag) {
        this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
      },

      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },

      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.dynamicTags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      }
    }
  }
</script>

<style scoped>
  .el-tag+.el-tag {
    margin-left: 10px;
  }

  .button-new-tag {
    margin-left: 0;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }

  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>



